import { Box, Container, Link, Typography } from "@mui/material";
import Logo from "./logo";
import GitHubIcon from "@mui/icons-material/GitHub";
import { YouTube } from "@mui/icons-material";

const linkStyling = {
  textDecoration: "none",
  "& :hover": {
    "& #link-name": { color: "#F9F9F9" },
  },
};

const renderLink = (name: string, href: string, icon: JSX.Element): JSX.Element => {
  return (
    <Link href={href} target="_blank" sx={linkStyling}>
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          columnGap: 1,
        }}
      >
        {icon}
        <Typography id="link-name" sx={{ fontSize: 24 }}>
          {name}
        </Typography>
      </Box>
    </Link>
  );
};

export default function Home(): JSX.Element {
  return (
    <Container maxWidth="md">
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          height: "100vh",
          maxHeight: "100vh",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          rowGap: 6,
        }}
      >
        <Logo />
        <Typography sx={{ textAlign: "justify" }}>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur bibendum ullamcorper
          velit eget fringilla. Nam a iaculis felis, sed egestas odio. In faucibus pharetra magna,
          vitae hendrerit sapien suscipit et. Suspendisse ac lectus vitae ex rutrum feugiat at vitae
          urna. Integer vitae imperdiet quam. Proin dapibus est vitae vestibulum ornare. In hac
          habitasse platea dictumst. Maecenas pulvinar risus quis elit dictum ultricies. Nunc odio
          lacus, scelerisque vitae maximus ac, ullamcorper eu dolor. Quisque vehicula libero vitae
          sagittis cursus. Sed vehicula nulla id libero egestas vulputate. Ut sollicitudin lorem
          sapien, ut elementum elit iaculis pellentesque. Fusce metus felis, bibendum eu nunc.
        </Typography>
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-evenly",
            width: "100%",
          }}
        >
          {renderLink(
            "GitHub",
            "https://github.com/TonyN96/fpl-zone/",
            <GitHubIcon fontSize="large" color="info" />
          )}
          {renderLink(
            "Trello",
            "https://trello.com/fplzone",
            <img
              src={`${process.env.PUBLIC_URL}/assets/images/trello-icon.png`}
              alt="trello-icon"
              height="40px"
            />
          )}
          {renderLink("Demo", "#", <YouTube fontSize="large" color="info" />)}
        </Box>
        <Typography sx={linkStyling}>
          An app developed by{" "}
          <Link sx={linkStyling} target="_blank" href="https://github.com/TonyN96">
            <Typography id="link-name" sx={{ display: "inline" }}>
              Tony Naughton
            </Typography>
          </Link>
        </Typography>
      </Box>
    </Container>
  );
}
